			swiperList:[
				{
					'imgUrl':'../../static/swiper.jpg',
					'title':'白云朵朵！'
				},
				{
					'imgUrl':'../../static/list.jpg',
					'title':'蓝天绿树！'
				},
				{
					'imgUrl':'../../static/user.jpg',
					'title':'boy！'
				}
			]
		}
	},

	methods: {
